<template>
<div id="test">
	<div ref="bb">
		<span class="has" ref="has">jas</span>
	</div>
	<button @click="add">test</button>
	<ul>
		<li>1</li>
		<li>2</li>
		<li>3</li>
	</ul>
	<p class="h">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Magni, maxime!</p>
</div>
</template>
<script>
	export default {
		methods:{
			add (){
				var bb=this.$refs["bb"];
				var has=this.$refs["has"];
				var newobj=document.createElement("div");
				newobj.className="enen";
				newobj.innerHTML='ejiwjriwjri';
				bb.insertBefore(newobj,has)

			}
		}
	}
</script>
<style scoped>
div#test{
	border: 1px solid red;
}
.enen{
	height: 200px;
	background: yellowgreen;
}
</style>


